/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			.mkdf-quantity-buttons {
			    border: none;
			    margin: 0;
			    padding: 0;
			    width: 104px;
			}
			tr {
				td.product-quantity {
					.mkdf-quantity-buttons {
						.mkdf-quantity-label {
							display: none;
						}
					}
				}
			}

			.mkdf-quantity-buttons {
				padding: 0 10px;
			}
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
						margin: 0 17px 0 0;
						padding: 9px 20px;
					}
				}
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {

		.cart-collaterals {

			.wc-proceed-to-checkout {
				
				a {
					width: 100%;
					text-align: center;
				}
			}
		}
		
		table.cart {
			width: 300px;
			overflow: scroll;
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
						&.product-name{
							letter-spacing: .05em;
						}
					}

				}
				

			}

			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 160px;
						padding: 13px 15px;
						margin: 0 10px 20px 0;
						width: 100%;
					}
					input[type=submit] {
						padding: 13px 23px;
						width: 100%;
					}
				}
				 button[type=submit]:not(.mkdf-woo-search-widget-button) {
		            margin-top: 15px;
	                padding: 13px 30px;
	                width: 100%;
	        }
				
				.coupon,
				> input,
				> button{
					float: none;
					display: block;
					width: 100%;
				}
			}
		}
		&.woocommerce-cart{
			.mkdf-container-inner{
				width: 340px;
			}
		}
	}

}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */

   