.mkdf-prod-cats-holder{

    @include ipad-landscape{
        $standard_list_columns: ('four','five', 'six');

        @for $i from 0 to length($standard_list_columns) {
            &.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
                $column_width: 100%/3;

                .mkdf-prod-cat{
                    width: $column_width;
                }
            }
        }
    }

    @include ipad-portrait{
        $standard_list_columns: ('three','four','five', 'six');

        @for $i from 0 to length($standard_list_columns) {
            &.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
                $column_width: 100%/2;

                .mkdf-prod-cat{
                    width: $column_width;
                }
            }
        }
    }

    @include phone-landscape{
        $standard_list_columns: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($standard_list_columns) {
            &.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
                $column_width: 100%;

                .mkdf-prod-cat{
                    width: $column_width;
                }
            }
        }
    }

}