/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .woocommerce-page .mkdf-content,
    div.woocommerce {
        .mkdf-quantity-buttons{

            .mkdf-quantity-input,
            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                font-size: 13px;
            }

            .mkdf-quantity-input {
                height: 38px;
                width: 38px;
                // margin: 0 19px 0 0;
                line-height: 36px;
            }

            .mkdf-quantity-minus,   
            .mkdf-quantity-plus {
                width: 19px;
                height: 19px;
                line-height: 17px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */