@include phone-landscape {

    .mkdf-pl-holder {
        
        &.mkdf-standard-layout {
            $standard_list_columns: ('two', 'three', 'four', 'five', 'six');
    
            @for $i from 0 to length($standard_list_columns) {
                &.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
                    $column_width: 100%;
            
                    .mkdf-pli {
                        width: $column_width;
                    }
                }
            }
        }
        
        &.mkdf-masonry-layout {
            $masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
    
            @for $i from 0 to length($masonry_list_columns) {
                &.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
	                
                    .mkdf-pli,
                    .mkdf-pl-sizer,
                    .mkdf-pli.mkdf-woo-image-large-width,
                    .mkdf-pli.mkdf-woo-image-large-width-height {
	                    width: 100%;
                    }
                }
            }
        }

		.mkdf-pl-categories{
			width: 60%;
		}

		.mkdf-pl-ordering-outer{
			width: 40%;
		}
    }
}
/* ==========================================================================
   Product List shortcode responsive styles - end
   ========================================================================== */